.loginContainer {
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  padding: 2rem;
  text-align: center;
  width: 300px;
  margin: 0 auto;
}

.loginContainer h1 {
  margin-bottom: 1rem;
  color: #333;
}

form label {
  display: block;
  margin-bottom: 0.5rem;
  color: #555;
}

form input[type='text'],
form input[type='password'] {
  width: 100%;
  padding: 0.5rem;
  margin-bottom: 1rem;
  border: 1px solid #ccc;
  border-radius: 3px;
}

form button {
  background-color: #333;
  color: #fff;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  width: 100%;
}

button:hover {
  background-color: #555;
}
